'use client'

import { useState } from 'react'

export default function Dashboard() {
  const [activeTab, setActiveTab] = useState<'translate' | 'content' | 'chat'>('translate')
  
  // Translation state
  const [translateText, setTranslateText] = useState('')
  const [sourceLang, setSourceLang] = useState('en')
  const [targetLang, setTargetLang] = useState('es')
  const [translationResult, setTranslationResult] = useState<any>(null)
  const [translating, setTranslating] = useState(false)

  // Content generation state
  const [contentType, setContentType] = useState('email')
  const [contentPrompt, setContentPrompt] = useState('')
  const [contentTone, setContentTone] = useState('professional')
  const [contentLanguage, setContentLanguage] = useState('en')
  const [contentResult, setContentResult] = useState<any>(null)
  const [generating, setGenerating] = useState(false)

  // Chat state
  const [chatMessage, setChatMessage] = useState('')
  const [chatHistory, setChatHistory] = useState<any[]>([])
  const [chatting, setChatting] = useState(false)

  const languages = [
    { code: 'en', name: 'English' },
    { code: 'es', name: 'Spanish' },
    { code: 'fr', name: 'French' },
    { code: 'de', name: 'German' },
    { code: 'it', name: 'Italian' },
    { code: 'pt', name: 'Portuguese' },
    { code: 'ja', name: 'Japanese' },
    { code: 'zh', name: 'Chinese' },
    { code: 'ko', name: 'Korean' },
    { code: 'ar', name: 'Arabic' },
    { code: 'hi', name: 'Hindi' },
    { code: 'ru', name: 'Russian' },
  ]

  const handleTranslate = async () => {
    setTranslating(true)
    try {
      const response = await fetch('/api/ai/translate', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          text: translateText,
          sourceLang,
          targetLang,
        }),
      })
      const data = await response.json()
      setTranslationResult(data)
    } catch (error) {
      console.error('Translation error:', error)
    } finally {
      setTranslating(false)
    }
  }

  const handleGenerateContent = async () => {
    setGenerating(true)
    try {
      const response = await fetch('/api/ai/content', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          type: contentType,
          prompt: contentPrompt,
          tone: contentTone,
          language: contentLanguage,
        }),
      })
      const data = await response.json()
      setContentResult(data)
    } catch (error) {
      console.error('Content generation error:', error)
    } finally {
      setGenerating(false)
    }
  }

  const handleChat = async () => {
    if (!chatMessage.trim()) return
    
    setChatting(true)
    const userMessage = { role: 'user', content: chatMessage, timestamp: new Date() }
    setChatHistory([...chatHistory, userMessage])
    setChatMessage('')

    try {
      const response = await fetch('/api/ai/chat', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          conversationId: 'dashboard-' + Date.now(),
          channel: 'website',
          message: chatMessage,
        }),
      })
      const data = await response.json()
      const aiMessage = {
        role: 'assistant',
        content: data.data?.reply || data.data?.response || data.data?.message || 'No response',
        sentiment: data.data?.sentiment,
        intent: data.data?.intent,
        timestamp: new Date(),
      }
      setChatHistory([...chatHistory, userMessage, aiMessage])
    } catch (error) {
      console.error('Chat error:', error)
    } finally {
      setChatting(false)
    }
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-blue-50 via-white to-purple-50">
      <div className="max-w-7xl mx-auto px-4 py-8">
        {/* Header */}
        <div className="mb-8">
          <h1 className="text-4xl font-bold text-gray-900 mb-2">RISIVO CRM Dashboard</h1>
          <p className="text-gray-600">Test all AI features in real-time</p>
        </div>

        {/* Tab Navigation */}
        <div className="flex gap-2 mb-6 border-b border-gray-200">
          <button
            onClick={() => setActiveTab('translate')}
            className={`px-6 py-3 font-medium transition-colors ${
              activeTab === 'translate'
                ? 'text-blue-600 border-b-2 border-blue-600'
                : 'text-gray-600 hover:text-gray-900'
            }`}
          >
            üåç Translation
          </button>
          <button
            onClick={() => setActiveTab('content')}
            className={`px-6 py-3 font-medium transition-colors ${
              activeTab === 'content'
                ? 'text-blue-600 border-b-2 border-blue-600'
                : 'text-gray-600 hover:text-gray-900'
            }`}
          >
            ‚úçÔ∏è Content AI
          </button>
          <button
            onClick={() => setActiveTab('chat')}
            className={`px-6 py-3 font-medium transition-colors ${
              activeTab === 'chat'
                ? 'text-blue-600 border-b-2 border-blue-600'
                : 'text-gray-600 hover:text-gray-900'
            }`}
          >
            üí¨ Chatbot
          </button>
        </div>

        {/* Translation Tab */}
        {activeTab === 'translate' && (
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
            {/* Input Panel */}
            <div className="bg-white rounded-lg shadow-md p-6">
              <h2 className="text-xl font-bold text-gray-900 mb-4">Translate Text</h2>
              
              <div className="space-y-4">
                <div className="grid grid-cols-2 gap-4">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">
                      From
                    </label>
                    <select
                      value={sourceLang}
                      onChange={(e) => setSourceLang(e.target.value)}
                      className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                    >
                      {languages.map((lang) => (
                        <option key={lang.code} value={lang.code}>
                          {lang.name}
                        </option>
                      ))}
                    </select>
                  </div>
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">
                      To
                    </label>
                    <select
                      value={targetLang}
                      onChange={(e) => setTargetLang(e.target.value)}
                      className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                    >
                      {languages.map((lang) => (
                        <option key={lang.code} value={lang.code}>
                          {lang.name}
                        </option>
                      ))}
                    </select>
                  </div>
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Text to Translate
                  </label>
                  <textarea
                    value={translateText}
                    onChange={(e) => setTranslateText(e.target.value)}
                    rows={6}
                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                    placeholder="Enter text to translate..."
                  />
                </div>

                <button
                  onClick={handleTranslate}
                  disabled={!translateText || translating}
                  className="w-full py-3 bg-blue-600 text-white font-medium rounded-md hover:bg-blue-700 disabled:bg-gray-400 disabled:cursor-not-allowed transition-colors"
                >
                  {translating ? 'Translating...' : 'Translate'}
                </button>
              </div>
            </div>

            {/* Result Panel */}
            <div className="bg-white rounded-lg shadow-md p-6">
              <h2 className="text-xl font-bold text-gray-900 mb-4">Result</h2>
              
              {translationResult ? (
                <div className="space-y-4">
                  {translationResult.success ? (
                    <>
                      <div className="bg-green-50 border border-green-200 rounded-md p-4">
                        <div className="text-lg text-gray-900 mb-2">
                          {translationResult.data.translatedText}
                        </div>
                      </div>
                      
                      <div className="grid grid-cols-2 gap-4 text-sm">
                        <div className="bg-gray-50 p-3 rounded">
                          <div className="text-gray-600 mb-1">Provider</div>
                          <div className="font-medium">{translationResult.data.provider}</div>
                        </div>
                        <div className="bg-gray-50 p-3 rounded">
                          <div className="text-gray-600 mb-1">Cost</div>
                          <div className="font-medium">${translationResult.data.cost.toFixed(6)}</div>
                        </div>
                        <div className="bg-gray-50 p-3 rounded">
                          <div className="text-gray-600 mb-1">Quality Score</div>
                          <div className="font-medium">{translationResult.data.qualityScore || 'N/A'}</div>
                        </div>
                        <div className="bg-gray-50 p-3 rounded">
                          <div className="text-gray-600 mb-1">Cached</div>
                          <div className="font-medium">{translationResult.data.cached ? 'Yes' : 'No'}</div>
                        </div>
                      </div>
                    </>
                  ) : (
                    <div className="bg-red-50 border border-red-200 rounded-md p-4 text-red-700">
                      Error: {translationResult.error}
                    </div>
                  )}
                </div>
              ) : (
                <div className="text-center py-12 text-gray-400">
                  Translation result will appear here
                </div>
              )}
            </div>
          </div>
        )}

        {/* Content Generation Tab */}
        {activeTab === 'content' && (
          <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
            {/* Input Panel */}
            <div className="bg-white rounded-lg shadow-md p-6">
              <h2 className="text-xl font-bold text-gray-900 mb-4">Generate Content</h2>
              
              <div className="space-y-4">
                <div className="grid grid-cols-2 gap-4">
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">
                      Content Type
                    </label>
                    <select
                      value={contentType}
                      onChange={(e) => setContentType(e.target.value)}
                      className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                    >
                      <option value="email">Email</option>
                      <option value="social">Social Media</option>
                      <option value="blog">Blog Post</option>
                      <option value="ad">Advertisement</option>
                      <option value="website">Website Copy</option>
                    </select>
                  </div>
                  <div>
                    <label className="block text-sm font-medium text-gray-700 mb-2">
                      Tone
                    </label>
                    <select
                      value={contentTone}
                      onChange={(e) => setContentTone(e.target.value)}
                      className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                    >
                      <option value="professional">Professional</option>
                      <option value="casual">Casual</option>
                      <option value="urgent">Urgent</option>
                      <option value="friendly">Friendly</option>
                    </select>
                  </div>
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Language
                  </label>
                  <select
                    value={contentLanguage}
                    onChange={(e) => setContentLanguage(e.target.value)}
                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                  >
                    {languages.map((lang) => (
                      <option key={lang.code} value={lang.code}>
                        {lang.name}
                      </option>
                    ))}
                  </select>
                </div>

                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Content Prompt
                  </label>
                  <textarea
                    value={contentPrompt}
                    onChange={(e) => setContentPrompt(e.target.value)}
                    rows={6}
                    className="w-full px-3 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                    placeholder="Describe what you want to generate..."
                  />
                </div>

                <button
                  onClick={handleGenerateContent}
                  disabled={!contentPrompt || generating}
                  className="w-full py-3 bg-blue-600 text-white font-medium rounded-md hover:bg-blue-700 disabled:bg-gray-400 disabled:cursor-not-allowed transition-colors"
                >
                  {generating ? 'Generating...' : 'Generate Content'}
                </button>
              </div>
            </div>

            {/* Result Panel */}
            <div className="bg-white rounded-lg shadow-md p-6">
              <h2 className="text-xl font-bold text-gray-900 mb-4">Generated Content</h2>
              
              {contentResult ? (
                <div className="space-y-4">
                  {contentResult.success ? (
                    <>
                      <div className="bg-green-50 border border-green-200 rounded-md p-4">
                        <div className="text-gray-900 whitespace-pre-wrap">
                          {contentResult.data.content}
                        </div>
                      </div>
                      
                      <div className="grid grid-cols-2 gap-4 text-sm">
                        <div className="bg-gray-50 p-3 rounded">
                          <div className="text-gray-600 mb-1">Tokens Used</div>
                          <div className="font-medium">{contentResult.data.tokensUsed || 'N/A'}</div>
                        </div>
                        <div className="bg-gray-50 p-3 rounded">
                          <div className="text-gray-600 mb-1">Cost</div>
                          <div className="font-medium">${contentResult.data.cost?.toFixed(4) || 'N/A'}</div>
                        </div>
                      </div>
                    </>
                  ) : (
                    <div className="bg-red-50 border border-red-200 rounded-md p-4 text-red-700">
                      Error: {contentResult.error}
                    </div>
                  )}
                </div>
              ) : (
                <div className="text-center py-12 text-gray-400">
                  Generated content will appear here
                </div>
              )}
            </div>
          </div>
        )}

        {/* Chat Tab */}
        {activeTab === 'chat' && (
          <div className="max-w-4xl mx-auto">
            <div className="bg-white rounded-lg shadow-md overflow-hidden">
              <div className="bg-gradient-to-r from-blue-600 to-purple-600 text-white p-4">
                <h2 className="text-xl font-bold">AI Virtual Assistant</h2>
                <p className="text-sm opacity-90">Ask me anything about RISIVO CRM</p>
              </div>

              {/* Chat History */}
              <div className="h-96 overflow-y-auto p-4 space-y-4 bg-gray-50">
                {chatHistory.length === 0 ? (
                  <div className="text-center py-12 text-gray-400">
                    Start a conversation...
                  </div>
                ) : (
                  chatHistory.map((msg, idx) => (
                    <div
                      key={idx}
                      className={`flex ${msg.role === 'user' ? 'justify-end' : 'justify-start'}`}
                    >
                      <div
                        className={`max-w-md px-4 py-2 rounded-lg ${
                          msg.role === 'user'
                            ? 'bg-blue-600 text-white'
                            : 'bg-white text-gray-900 border border-gray-200'
                        }`}
                      >
                        <div className="whitespace-pre-wrap">{msg.content}</div>
                        {msg.sentiment && (
                          <div className="text-xs mt-2 opacity-70">
                            Sentiment: {msg.sentiment} | Intent: {msg.intent}
                          </div>
                        )}
                      </div>
                    </div>
                  ))
                )}
              </div>

              {/* Chat Input */}
              <div className="p-4 border-t border-gray-200">
                <div className="flex gap-2">
                  <input
                    type="text"
                    value={chatMessage}
                    onChange={(e) => setChatMessage(e.target.value)}
                    onKeyPress={(e) => e.key === 'Enter' && handleChat()}
                    placeholder="Type your message..."
                    className="flex-1 px-4 py-2 border border-gray-300 rounded-md focus:ring-2 focus:ring-blue-500 focus:border-transparent"
                  />
                  <button
                    onClick={handleChat}
                    disabled={!chatMessage.trim() || chatting}
                    className="px-6 py-2 bg-blue-600 text-white font-medium rounded-md hover:bg-blue-700 disabled:bg-gray-400 disabled:cursor-not-allowed transition-colors"
                  >
                    {chatting ? 'Sending...' : 'Send'}
                  </button>
                </div>
              </div>
            </div>
          </div>
        )}
      </div>
    </div>
  )
}
